import { Component, OnInit, OnDestroy, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';
import { ProfesseursCompletDTO } from 'src/app/DTO/ProfesseursCompletDTO';
import { ProfesseursCompletService } from 'src/app/services/professeurs-complet.service';
import { Professeurs } from 'src/app/Models/Professeurs';
import { ProfesseursComplet } from 'src/app/Models/ProfesseursComplet';
import { QuestionsService } from 'src/app/services/questions.service';
import { SolutionsService } from 'src/app/services/solutions.service';
import { Questions } from 'src/app/Models/Questions';
import { Solutions } from 'src/app/Models/Solutions';
import { QuestionDTO } from 'src/app/DTO/QuestionsDTO';

@Component({
  selector: 'app-add-question-form',
  templateUrl: './add-question-form.component.html',
  styleUrls: ['./add-question-form.component.css']
})
export class AddQuestionFormComponent implements OnInit, OnDestroy {

  isMultiple: boolean = false;
  addQuestionForm: FormGroup;
  numberLoop: number[];
  @Output()
  questionCreated: EventEmitter<Questions> = new EventEmitter<Questions>();
  private NB_SOLUTIONS_MAX: number = 5;
  private user: Professeurs = JSON.parse(localStorage.getItem("currentUser"));
  private _professeursComplet: ProfesseursComplet;
  private subscriptions: Subscription[] = [];

  constructor(private formBuilder: FormBuilder,
    private professeurCompletService: ProfesseursCompletService,
    private questionsService: QuestionsService,
    private solutionsService: SolutionsService) { }

  ngOnInit() {
    this.initForm();
    this.getProfesseurComplet();
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  emitCreatedQuestion(question: Questions) {
    this.questionCreated.next(question);
  }

  getProfesseurComplet() {
    const id = JSON.parse(localStorage.getItem("currentUser")).id; //id of logged in prof
    const sub = this.professeurCompletService
      .get(id)
      .subscribe(profC => {
        this._professeursComplet = new ProfesseursComplet().fromProfesseursCompletDTO(profC);
        console.log(profC);
      });
    this.subscriptions.push(sub);
  }

  initForm() {
    this.addQuestionForm = this.formBuilder.group({
      name: ['', Validators.required],
      cours: ['', Validators.required],
      nbSolutions: [''],
      solution: ['', Validators.required]
    })
  }

  toggleMultiple() {
    this.isMultiple = !this.isMultiple;
    if (this.isMultiple) {
      this.addQuestionForm.removeControl('solution');
      this.addQuestionForm.addControl('answer', new FormControl('', Validators.required));

    } else {
      this.addQuestionForm.addControl('solution', new FormControl('', Validators.required));
      this.addQuestionForm.removeControl('answer');
      this.removePreviousControls();
    }
  }

  createArray() {
    console.log(this.addQuestionForm.get('nbSolutions').value);
    let nbSol: number = this.addQuestionForm.get('nbSolutions').value;
    this.removePreviousControls();
    if (nbSol == null || nbSol < 2) {
      nbSol = 2;
    } else if (nbSol > 5) {
      nbSol = 5;
    }
    this.addNewControls(nbSol);
    this.numberLoop = Array(nbSol).fill(0).map((x, i) => i + 1);
  }

  removePreviousControls() {
    for (let index = 0; index < this.NB_SOLUTIONS_MAX; index++) {
      let controlName: string = `solution${index + 1}`;
      if (this.addQuestionForm.contains(controlName)) {
        this.addQuestionForm.removeControl(controlName);
      }
    }
  }

  addNewControls(controlNumber: number) {
    for (let index = 0; index < controlNumber; index++) {
      let controlName: string = `solution${index + 1}`;
      this.addQuestionForm.addControl(controlName, new FormControl('', Validators.required))
    }
  }

  onSubmit() {
    console.log(this.addQuestionForm.controls);
    this.getAllField();
  }

  getAllField() {
    const name: string = this.addQuestionForm.get('name').value;
    const cours: number = +this.addQuestionForm.get('cours').value;
    console.log(cours);
    const author: number = this.user.id;
    let answer: number = 1;
    let solutions: string[] = [];
    if (this.isMultiple) {
      for (let index = 0; index < this.numberLoop.length; index++) {
        let controlName: string = `solution${index + 1}`;
        solutions.push(this.addQuestionForm.get(controlName).value);
      }
      answer = this.addQuestionForm.get('answer').value;
    } else {
      solutions.push(this.addQuestionForm.get('solution').value);
    }
    this.postQuestion(name, cours, author, solutions, answer);
  }

  postQuestion(name: string, cours: number, author: number, solutions: string[], answer: number) {
    const question: Questions = this.buildQuestion(name, cours, author);
    const sub = this.questionsService
      .post(question.toQuestionsDTO())
      .subscribe(
        questionDto => {
          const listSolutions: Solutions[] = this.buildSolutions(solutions, answer, questionDto.id);
          let i = 0;
          listSolutions.forEach(solution => {
            let subSolution = this.solutionsService
              .post(solution.toSolutionsDTO())
              .subscribe(solution => {
                if (listSolutions.length == i + 1) {
                  this.emitCreatedQuestion(new Questions().fromQuestionsDTO(questionDto));
                }
                i++;
              });

            this.subscriptions.push(subSolution);
          });
        }
      );
    this.addQuestionForm.reset();
    this.subscriptions.push(sub);
  }

  buildSolutions(solutions: string[], answer: number, id: number) {
    let listSolutions: Solutions[] = [];
    for (let index = 0; index < solutions.length; index++) {
      let sol: Solutions = new Solutions();
      sol.text = solutions[index];
      sol.id_questions = id;
      if (index + 1 == answer) {
        sol.answer = 1;
      } else {
        sol.answer = 0;
      }
      listSolutions.push(sol);
    }
    return listSolutions;
  }

  buildQuestion(name: string, cours: number, author: number): Questions {
    const question: Questions = new Questions();
    question.name = name;
    question.cours = cours;
    question.author = author;
    return question;
  }

  get professeursComplet(): ProfesseursComplet {
    return this._professeursComplet;
  }

  set professeursComplet(obj: ProfesseursComplet) {
    this._professeursComplet = obj;
  }

}
