import { Tests } from './Tests';
import { QuestionsComplet } from './QuestionsComplet';
import { TestsCompletDTO } from '../DTO/TestsCompletDTO';

export class TestsComplet {
    private _test: Tests;
    private _questionComplets: QuestionsComplet[];

    constructor(test: Tests = null, questionsComplets: QuestionsComplet[] = []) {
        this._test = test;
        this._questionComplets = questionsComplets;
    }

    toTestsCompletDTO(): TestsCompletDTO {
        return {
            test: this._test,
            questionComplets: this._questionComplets
        }
    }

    fromTestsCompletDTO(dto: TestsCompletDTO): TestsComplet {
        Object.assign(this, dto);
        return this;
    }

    public get test(): Tests {
        return this._test;
    }

    public set test(value: Tests) {
        this._test = value;
    }

    get questionComplets(): QuestionsComplet[] {
        return this._questionComplets;
    }
    set questionComplets(value: QuestionsComplet[]) {
        this._questionComplets = value;
    }


}