import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { QuestionDTO } from '../DTO/QuestionsDTO';

const URL_API: string = "api/questions"

@Injectable({
  providedIn: 'root'
})
export class QuestionsService {

  constructor(private http: HttpClient) { }

  query(): Observable<QuestionDTO[]> {
    return this.http.get<QuestionDTO[]>(URL_API);
  }

  get(id: number): Observable<QuestionDTO> {
    return this.http.get<QuestionDTO>(`${URL_API}/${id}`);
  }

  post(questionDTO: QuestionDTO): Observable<QuestionDTO> {
    return this.http.post<QuestionDTO>(URL_API, questionDTO)
  }

  put(questionDTO: QuestionDTO): Observable<any> {
    return this.http.put(URL_API, questionDTO);
  }

  delete(id: number): Observable<any> {
    return this.http.delete(`${URL_API}/${id}`);
  }
}
