import { Injectable } from '@angular/core';
import { ProfesseursCompletDTO } from '../DTO/ProfesseursCompletDTO';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

const URL_API: string = "api/professeursComplet";

@Injectable({
  providedIn: 'root'
})
export class ProfesseursCompletService {

  constructor(private http: HttpClient) { }

  query(): Observable<ProfesseursCompletDTO[]> {
    return this.http.get<ProfesseursCompletDTO[]>(URL_API);
  }

  get(id: number): Observable<ProfesseursCompletDTO> {
    return this.http.get<ProfesseursCompletDTO>(`${URL_API}/${id}`);
  }
}
