import { SolutionsDTO } from '../DTO/SolutionsDTO';

export class Solutions {
    private _id: number;
    private _text: string;
    private _id_questions: number;
    private _answer: number;

    constructor(id: number = -1, text: string = "", id_questions: number = -1, answer: number = -1) {
        this._id = id;
        this._text = text;
        this._id_questions = id_questions;
        this._answer = answer;
    }

    toSolutionsDTO(): SolutionsDTO {
        return {
            id: this._id,
            text: this._text,
            id_questions: this._id_questions,
            answer: this._answer
        };
    }

    fromSolutionsDTO(dto: SolutionsDTO): Solutions {
        Object.assign(this, dto);
        return this;
    }

    equals(obj): boolean {
        if (obj instanceof Solutions) {
            return this._id === (<Solutions>obj)._id;
        }
        return false;
    }

    get id(): number {
        return this._id;
    }

    set id(value: number) {
        this._id = value;
    }

    get text(): string {
        return this._text;
    }

    set text(value: string) {
        this._text = value;
    }

    get id_questions(): number {
        return this._id_questions;
    }

    set id_questions(value: number) {
        this._id_questions = value;
    }

    get answer(): number {
        return this._answer;
    }

    set answer(value: number) {
        this._answer = value;
    }
}
