import { Pipe, PipeTransform } from '@angular/core';
import { QuestionsCompletDTO } from '../DTO/QuestionsCompletDTO';

@Pipe({
  name: 'filterSearch'
})
export class FilterSearchPipe implements PipeTransform {

  transform(questionsComplets: QuestionsCompletDTO[], searchInput: string): QuestionsCompletDTO[] {
    let qC: QuestionsCompletDTO[] = [];
    console.log(questionsComplets);
    console.log(searchInput);
    if (searchInput == '') {
      return questionsComplets;
    }
    questionsComplets.forEach(q => {
      console.log(q.question.name.toLowerCase());
      if (q.question.name.toLowerCase().includes(searchInput.toLowerCase())) {
        qC.push(q);
        console.log(q.question.name);
      }
    })
    console.log(qC)
    return qC;
  }
}
