import { Professeurs } from './Professeurs';
import { Cours } from './Cours';
import { ProfesseursCompletDTO } from '../DTO/ProfesseursCompletDTO';

export class ProfesseursComplet {
    private _professeur: Professeurs;
    private _cours: Cours[];

    constructor(professeur: Professeurs = null, cours: Cours[] = []) {
        this._professeur = professeur;
        this._cours = cours;
    }

    toProfesseursCompletDTO(): ProfesseursCompletDTO {
        return {
            professeur: this._professeur,
            cours: this._cours
        };
    }

    fromProfesseursCompletDTO(dto: ProfesseursCompletDTO): ProfesseursComplet {
        Object.assign(this, dto);
        return this;
    }

    get professeurs(): Professeurs {
        return this._professeur;
    }

    set professeurs(obj: Professeurs) {
        this._professeur = obj;
    }

    get cours(): Cours[] {
        return this._cours;
    }

    set cours(obj: Cours[]) {
        this._cours = obj;
    }
}