import { Injectable } from '@angular/core';
import { TestsCompletDTO } from '../DTO/TestsCompletDTO';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

const URL_API: string = "api/testscomplet"

@Injectable({
  providedIn: 'root'
})
export class TestsCompletService {

  constructor(private http: HttpClient) { }

  query(): Observable<TestsCompletDTO[]> {
    return this.http.get<TestsCompletDTO[]>(URL_API);
  }

  get(id: number): Observable<TestsCompletDTO> {
    return this.http.get<TestsCompletDTO>(`${URL_API}/${id}`);
  }

}
