import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CoursDTO } from '../DTO/CoursDTO';

const URL_API = "api/cours"

@Injectable({
  providedIn: 'root'
})
export class CoursService {

  constructor(private http: HttpClient) { }

  query(): Observable<CoursDTO[]> {
    return this.http.get<CoursDTO[]>(URL_API);
  }

  get(id: number): Observable<CoursDTO> {
    return this.http.get<CoursDTO>(`${URL_API}/${id}`);
  }
}
