import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Professeurs } from 'src/app/Models/Professeurs';
import { Tests } from 'src/app/Models/Tests';
import { ProfesseursComplet } from 'src/app/Models/ProfesseursComplet';
import { Router } from '@angular/router';
import { QuestionsTestsService } from 'src/app/services/questions-tests.service';
import { Observable } from 'rxjs';
import { Questions_TestsDTO } from 'src/app/DTO/Questions_TestsDTO';
import { CoursService } from 'src/app/services/cours.service';
import { ProfesseursService } from 'src/app/services/professeurs.service';
import { Cours } from 'src/app/Models/Cours';
import { CoursDTO } from 'src/app/DTO/CoursDTO';
import { ProfesseursDTO } from 'src/app/DTO/ProfesseursDTO';

@Component({
  selector: 'app-test-detail',
  templateUrl: './test-detail.component.html',
  styleUrls: ['./test-detail.component.css']
})
export class TestDetailComponent implements OnInit {

  @Input()
  private user: Professeurs;
  @Input()
  private test: Tests;
  @Input()
  private professeurComplet: ProfesseursComplet;
  @Output()
  testDeleted: EventEmitter<Tests> = new EventEmitter<Tests>();
  questions_tests$: Observable<Questions_TestsDTO[]>;
  cours$: Observable<CoursDTO>;
  professeurs$: Observable<ProfesseursDTO>;

  constructor(private router: Router,
    private questions_testsService: QuestionsTestsService,
    private coursService: CoursService,
    private professeursService: ProfesseursService) { }

  ngOnInit() {
    this.getQuestionsTestsObservable();
    this.getCoursObservable();
    this.getProfesseursObservable();
  }

  getProfesseursObservable() {
    this.cours$ = this.coursService.get(this.test.cours);
  }

  getCoursObservable() {
    this.professeurs$ = this.professeursService.get(this.test.author);
  }

  getQuestionsTestsObservable() {
    this.questions_tests$ = this.questions_testsService.getByTest(this.test.id);
  }

  emitDeletedTest() {
    const test: Tests = new Tests().fromTestsDTO(this.test);
    this.testDeleted.next(test);
  }

  onViewTest(id: number) {
    this.router.navigate(['/tests', 'view', id])
  }

}
