import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { QuestionsService } from 'src/app/services/questions.service';
import { SolutionsService } from 'src/app/services/solutions.service';
import { Observable, Subscription } from 'rxjs';
import { QuestionDTO } from 'src/app/DTO/QuestionsDTO';
import { SolutionsDTO } from 'src/app/DTO/SolutionsDTO';
import { Professeurs } from 'src/app/Models/Professeurs';
import { ProfesseursCompletDTO } from 'src/app/DTO/ProfesseursCompletDTO';
import { ProfesseursCompletService } from 'src/app/services/professeurs-complet.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Questions } from 'src/app/Models/Questions';
import { Solutions } from 'src/app/Models/Solutions';

@Component({
  selector: 'app-single-question',
  templateUrl: './single-question.component.html',
  styleUrls: ['./single-question.component.css']
})
export class SingleQuestionComponent implements OnInit, OnDestroy {

  modifyQuestionForm: FormGroup;
  question$: Observable<QuestionDTO>;
  solutions$: Observable<SolutionsDTO[]>;
  professeurComplet$: Observable<ProfesseursCompletDTO>;
  private rightAnswer: number;
  private previousRightAnswer: number;
  private unmodifiedQuestion: Questions;
  private subscriptions: Subscription[] = [];
  private user: Professeurs = JSON.parse(localStorage.getItem("currentUser"));

  constructor(private route: ActivatedRoute,
    private professeurCompletService: ProfesseursCompletService,
    private questionsService: QuestionsService,
    private solutionsService: SolutionsService,
    private formBuilder: FormBuilder) { }

  ngOnInit() {
    const id = +this.route.snapshot.params['id'];
    this.getProfesseurComplet(this.user.id);
    this.getQuestionObservable(id);
    this.getSolutionsObservable(id);
    this.initForm();
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  initForm() {
    this.modifyQuestionForm = this.formBuilder.group({
      nameInput: null,
      coursSelect: null,
    })
  }

  getProfesseurComplet(id: number) {
    this.professeurComplet$ = this.professeurCompletService.get(id);
  }

  getQuestionObservable(id: number) {
    this.question$ = this.questionsService.get(id);
    const sub = this.questionsService
      .get(id)
      .subscribe(questionDTO => {
        this.unmodifiedQuestion = new Questions().fromQuestionsDTO(questionDTO);
        this.modifyQuestionForm.get('coursSelect').setValue(this.unmodifiedQuestion.cours);
        console.log(this.unmodifiedQuestion);
      })
    this.subscriptions.push(sub);
  }

  getSolutionsObservable(idQuestion: number) {
    this.solutions$ = this.solutionsService.getByQuestion(idQuestion);
    const sub = this.solutionsService
      .getByQuestion(idQuestion)
      .subscribe(solutionsDTO => {
        let i: number = 0;
        solutionsDTO.forEach(solution => {
          i++;
          if (solution.answer == 1) {
            this.rightAnswer = solution.id;
            this.previousRightAnswer = solution.id;
          }
        })
        console.log(this.rightAnswer);
      })

    this.subscriptions.push(sub);
  }

  onSubmit() {
    const updatedQuestion: Questions = this.updatedQuestion(this.unmodifiedQuestion);
    console.log(updatedQuestion);
    const sub = this.questionsService
      .put(updatedQuestion.toQuestionsDTO())
      .subscribe(() => {
        this.launchNotification('This question has been modified ! ', 'success')
      });
    this.unmodifiedQuestion = updatedQuestion;
    this.subscriptions.push(sub);
  }

  onSave() {
    console.log(this.rightAnswer);
    /* Update Right Answer */
    const sub = this.solutionsService
      .get(this.rightAnswer)
      .subscribe(solution => {
        solution.answer = 1;
        const subSolution = this.solutionsService
          .put(solution)
          .subscribe(whatev => {
            console.log(solution);
            this.previousRightAnswer = solution.id;
          })
        this.subscriptions.push(subSolution);
      })
    this.subscriptions.push(sub);
    /* Remove previous right answer */
    const subPrevious = this.solutionsService
      .get(this.previousRightAnswer)
      .subscribe(solution => {
        solution.answer = 0;
        const subSolution = this.solutionsService
          .put(solution)
          .subscribe(whatev => {
            console.log(solution);
            this.launchNotification('The right answer has been modified ! ', 'success')
          })
        this.subscriptions.push(subSolution);
      })
    this.subscriptions.push(sub);
  }

  updatedQuestion(unmodifiedQuestion: Questions): Questions {
    unmodifiedQuestion.name = (this.modifyQuestionForm.get('nameInput').value == null ? unmodifiedQuestion.name : this.modifyQuestionForm.get('nameInput').value);
    unmodifiedQuestion.cours = (this.modifyQuestionForm.get('coursSelect').value == null ? unmodifiedQuestion.cours : +this.modifyQuestionForm.get('coursSelect').value);
    return unmodifiedQuestion;
  }

  deleteSolution($event: Solutions) {
    console.log($event);
    if (confirm("Do you want to delete this solution ?")) {
      const sub = this.solutionsService
        .delete($event.id)
        .subscribe(whatev => {
          /* Recall get to refresh solutions[] */
          /* Works */
          const idQuestion: number = +this.route.snapshot.params['id'];
          this.getSolutionsObservable(idQuestion);
          this.launchNotification('This solution has been deleted ! ', 'danger')
        });
      this.subscriptions.push(sub);
    }
    this.launchNotification('This solution has been deleted ! ', 'danger')
  }

  launchNotification(notificationMessage: string, type: string) {
    const notification = document.querySelector('.notification');
    const message = document.querySelector('.notification > p');
    console.log(message.textContent);
    message.textContent = notificationMessage;
    notification.classList.add('visible');
    notification.classList.add(type);
    const delay = 3500;
    setTimeout(function () {
      notification.classList.remove('visible');
    }, delay);
    setTimeout(function () {
      notification.classList.remove(type);
    }, 4000);
  }

}
