import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ProfesseursDTO } from '../DTO/ProfesseursDTO';

const URL_API: string = "api/professeurs"

@Injectable({
  providedIn: 'root'
})
export class ProfesseursService {

  constructor(private http: HttpClient) { }

  query(): Observable<ProfesseursDTO[]> {
    return this.http.get<ProfesseursDTO[]>(URL_API);
  }

  getAll(): Observable<ProfesseursDTO[]> {
    return this.http.get<ProfesseursDTO[]>(`${URL_API}/token/authorized`);
  }

  get(id: number): Observable<ProfesseursDTO> {
    return this.http.get<ProfesseursDTO>(`${URL_API}/${id}`);
  }

  post(professeursDTO: ProfesseursDTO): Observable<ProfesseursDTO> {
    return this.http.post<ProfesseursDTO>(URL_API, professeursDTO);
  }

  put(professeursDTO: ProfesseursDTO): Observable<any> {
    return this.http.put(URL_API, professeursDTO);
  }

  delete(id: number): Observable<any> {
    return this.http.delete(`${URL_API}/${id}`);
  }
}
