import { Pipe, PipeTransform } from '@angular/core';
import { QuestionsComplet } from '../Models/QuestionsComplet';
import { QuestionsCompletDTO } from '../DTO/QuestionsCompletDTO';

@Pipe({
  name: 'filterSelect',
})
export class FilterSelectPipe implements PipeTransform {

  transform(questionsComplets: QuestionsCompletDTO[], selectedOption: number): QuestionsCompletDTO[] {
    console.log(questionsComplets)
    let qC: QuestionsCompletDTO[] = [];
    if (selectedOption == 0) {
      return questionsComplets;
    }
    questionsComplets.forEach(q => {
      if (q.cours.id == selectedOption) {
        qC.push(q);
      }
    })
    console.log(qC)
    return qC;
  }
}
