import { Pipe, PipeTransform } from '@angular/core';
import { TestsDTO } from '../DTO/TestsDTO';

@Pipe({
  name: 'filterCoursTests'
})
export class FilterProfesseursCoursPipe implements PipeTransform {

  transform(tests: TestsDTO[], selectedOption: number): TestsDTO[] {
    let filteredTests: TestsDTO[] = [];
    if (selectedOption == 0) {
      return tests;
    }
    tests.forEach(test => {
      if (test.cours == selectedOption) {
        filteredTests.push(test);
      }
    })
    console.log(filteredTests)
    return filteredTests;
  }
}
