import { QuestionDTO } from '../DTO/QuestionsDTO';

export class Questions {
    private _id?: number;
    private _name: string;
    private _author: number;
    private _cours: number;

    constructor(id: number = -1, name: string = "", author: number = -1, cours: number = -1) {
        this._id = id;
        this._name = name;
        this._author = author;
        this._cours = cours;
    }

    toQuestionsDTO(): QuestionDTO {
        return {
            id: this._id,
            name: this._name,
            author: this._author,
            cours: this._cours
        };
    }

    fromQuestionsDTO(dto: QuestionDTO): Questions {
        Object.assign(this, dto);
        return this;
    }

    equals(obj): boolean {
        if (obj instanceof Questions) {
            return this._id === (<Questions>obj)._id;
        }
        return false;
    }

    get id(): number {
        return this._id;
    }

    set id(value: number) {
        this._id = value;
    }

    get name(): string {
        return this._name;
    }

    set name(value: string) {
        this._name = value;
    }

    get author(): number {
        return this._author;
    }

    set author(value: number) {
        this._author = value;
    }

    get cours(): number {
        return this._cours;
    }

    set cours(value: number) {
        this._cours = value;
    }
}
