import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { TestsDTO } from '../DTO/TestsDTO';

const URL_API: string = "api/tests"

@Injectable({
  providedIn: 'root'
})
export class TestsService {

  constructor(private http: HttpClient) { }

  query(): Observable<TestsDTO[]> {
    return this.http.get<TestsDTO[]>(URL_API);
  }

  get(id: number): Observable<TestsDTO> {
    return this.http.get<TestsDTO>(`${URL_API}/${id}`);
  }

  post(testsDTO: TestsDTO): Observable<TestsDTO> {
    return this.http.post<TestsDTO>(URL_API, testsDTO);
  }

  put(testsDTO: TestsDTO): Observable<any> {
    return this.http.put<any>(URL_API, testsDTO);
  }

  delete(id: number): Observable<any> {
    return this.http.delete(`${URL_API}/${id}`);
  }
}
