import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { Professeurs } from 'src/app/Models/Professeurs';
import { TestsDTO } from 'src/app/DTO/TestsDTO';
import { TestsService } from 'src/app/services/tests.service';
import { ProfesseursCompletDTO } from 'src/app/DTO/ProfesseursCompletDTO';
import { ProfesseursCompletService } from 'src/app/services/professeurs-complet.service';
import { ProfesseursComplet } from 'src/app/Models/ProfesseursComplet';
import { Tests } from 'src/app/Models/Tests';

@Component({
  selector: 'app-test-list',
  templateUrl: './test-list.component.html',
  styleUrls: ['./test-list.component.css']
})
export class TestListComponent implements OnInit, OnDestroy {

  selectedCoursOption: number = 0;
  tests$: Observable<TestsDTO[]>;
  professeursComplet$: Observable<ProfesseursCompletDTO>;
  searchInput: string = '';
  private user: Professeurs = JSON.parse(localStorage.getItem("currentUser"));
  private _professeursComplet: ProfesseursComplet;
  private subscriptions: Subscription[] = [];

  constructor(private testsService: TestsService,
    private professeursCompletService: ProfesseursCompletService) { }

  ngOnInit() {
    this.getTestsObservable();
    this.getProfesseursCompletObservable();
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  getProfesseursCompletObservable() {
    const id = this.user.id;
    this.professeursComplet$ = this.professeursCompletService.get(id);
    const sub = this.professeursCompletService
      .get(id)
      .subscribe(profC => {
        this._professeursComplet = new ProfesseursComplet().fromProfesseursCompletDTO(profC);
        console.log(profC);
      });
    this.subscriptions.push(sub);
  }

  getTestsObservable() {
    this.tests$ = this.testsService.query();
  }

  launchNotification() {
    const notification = document.querySelector('.notification');
    const message = document.querySelector('.notification > p');
    console.log(message.textContent);
    message.textContent = 'Test has been successfully created ! ';
    notification.classList.add('visible');
    const delay = 3500;
    setTimeout(function () {
      notification.classList.remove('visible');
    }, delay);
  }

  // resetSearch() {
  //   // reset after animation ends
  //   setTimeout(
  //     this.searchInput = ''
  //     , 1000);
  // }

  createTest($event: Tests) {
    console.log($event);
    const sub = this.testsService
      .post($event.toTestsDTO())
      .subscribe(test => {
        console.log(test);
        this.getTestsObservable();
        this.launchNotification();
      })
    this.subscriptions.push(sub);
  }

  deleteTest($event: Tests) {
    console.log($event);
    if (confirm("Do you want to delete this test ?")) {
      const sub = this.testsService
        .delete($event.id)
        .subscribe(whatev => {
          console.log(whatev);
          this.getTestsObservable();
        });
      this.subscriptions.push(sub);
    }
  }

  public get professeursComplet(): ProfesseursComplet {
    return this._professeursComplet;
  }

  public set professeursComplet(value: ProfesseursComplet) {
    this._professeursComplet = value;
  }

}
