import { Component, OnInit, OnDestroy } from '@angular/core';
import { QuestionsCompletService } from 'src/app/services/questions-complet.service';
import { QuestionsComplet } from 'src/app/Models/QuestionsComplet';
import { Subscription, Observable } from 'rxjs';
import { ProfesseursCompletService } from 'src/app/services/professeurs-complet.service';
import { ProfesseursComplet } from 'src/app/Models/ProfesseursComplet';
import { QuestionsCompletDTO } from 'src/app/DTO/QuestionsCompletDTO';
import { Professeurs } from 'src/app/Models/Professeurs';
import { QuestionsService } from 'src/app/services/questions.service';
import { Questions } from 'src/app/Models/Questions';
import { QuestionDTO } from 'src/app/DTO/QuestionsDTO';

@Component({
  selector: 'app-question-list',
  templateUrl: './question-list.component.html',
  styleUrls: ['./question-list.component.css']
})
export class QuestionListComponent implements OnInit, OnDestroy {
  private _questionsComplets: QuestionsComplet[];
  private _professeursComplet: ProfesseursComplet;
  private subscriptionsQuestions: Subscription[] = [];
  private subscriptionProfesseur: Subscription;
  searchInput: string = '';
  selectedOption: number = 0;
  observable$: Observable<QuestionsCompletDTO[]>;
  coursTeached = false;
  private user: Professeurs = JSON.parse(localStorage.getItem("currentUser"));


  constructor(private questionsCompletService: QuestionsCompletService,
    private professeursCompletService: ProfesseursCompletService,
    private questionsService: QuestionsService) { }

  ngOnInit() {
    this.getProfesseursComplet();
    //this.loadQuestionsComplets();
    this.getObservable();
  }
  getObservable() {
    this.observable$ = this.questionsCompletService
      .query();
  }

  getProfesseursComplet() {
    const id = JSON.parse(localStorage.getItem("currentUser")).id; //id of logged in prof
    const sub = this.professeursCompletService
      .get(id)
      .subscribe(profC => {
        this._professeursComplet = new ProfesseursComplet().fromProfesseursCompletDTO(profC);
        console.log(profC);

      });
    this.subscriptionProfesseur = sub;
  }

  loadQuestionsComplets() {
    const sub = this.questionsCompletService
      .query()
      .subscribe(qcs => {
        this._questionsComplets = qcs.map(qc =>
          new QuestionsComplet().fromQuestionsCompletDTO(qc));
        console.log(this._questionsComplets);
      });
    this.subscriptionsQuestions.push(sub);

  }

  ngOnDestroy(): void {
    for (let i = this.subscriptionsQuestions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptionsQuestions[i];
      subscription && subscription.unsubscribe();
      this.subscriptionsQuestions.pop();
    }
    this.subscriptionProfesseur.unsubscribe();
  }

  launchNotification() {
    const notification = document.querySelector('.notification');
    const message = document.querySelector('.notification > p');
    console.log(message.textContent);
    message.textContent = 'Question has been successfully created ! ';
    notification.classList.add('visible');
    const delay = 3500;
    setTimeout(function () {
      notification.classList.remove('visible');
    }, delay);
  }

  deleteQuestion($event: QuestionDTO) {
    console.log($event);
    if (confirm("Do you want to delete this question ?")) {
      const sub = this.questionsService
        .delete($event.id)
        .subscribe(whatev => {
          /* Recall Observable to refresh */
          this.getObservable();
        })
      this.subscriptionsQuestions.push(sub);
    }
  }

  createQuestion($event) {
    console.log($event);
    this.launchNotification();
    this.getObservable();
  }

  // resetSearch() {
  //   // reset after animation ends
  //   this.searchInput = ''
  // }

  get questionsComplets(): QuestionsComplet[] {
    return this._questionsComplets;
  }

  set questionsComplets(obj: QuestionsComplet[]) {
    this._questionsComplets = obj;
  }

  get professeursComplet(): ProfesseursComplet {
    return this._professeursComplet;
  }

  set professeursComplet(obj: ProfesseursComplet) {
    this._professeursComplet = obj;
  }

}
