import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { QuestionsComplet } from 'src/app/Models/QuestionsComplet';
import { Router } from '@angular/router';
import { ProfesseursComplet } from 'src/app/Models/ProfesseursComplet';
import { QuestionsCompletDTO } from 'src/app/DTO/QuestionsCompletDTO';
import { Professeurs } from 'src/app/Models/Professeurs';
import { Questions } from 'src/app/Models/Questions';
import { QuestionsService } from 'src/app/services/questions.service';
import { QuestionDTO } from 'src/app/DTO/QuestionsDTO';
import { QuestionsTestsService } from 'src/app/services/questions-tests.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-question-detail',
  templateUrl: './question-detail.component.html',
  styleUrls: ['./question-detail.component.css']
})
export class QuestionDetailComponent implements OnInit {

  @Input()
  private user: Professeurs;
  @Input()
  private questionComplet: QuestionsCompletDTO;
  @Input()
  private professeurComplet: ProfesseursComplet;
  @Input()
  private view: string;
  @Output()
  questionDeleted: EventEmitter<Questions> = new EventEmitter<Questions>();
  @Output()
  questionRemoved: EventEmitter<Questions> = new EventEmitter<Questions>();
  @Output()
  questionAdded: EventEmitter<Questions> = new EventEmitter<Questions>();
  private subscriptions: Subscription[] = [];
  private questionsOccurence: number;

  constructor(private router: Router,
    private questionsTestsService: QuestionsTestsService) { }

  ngOnInit() {
    console.log(this.view);
    this.getQuestions_Tests();
  }

  getQuestions_Tests() {
    this.questionsTestsService
      .getByQuestion(this.questionComplet.question.id)
      .subscribe(questions_tests => {
        this.questionsOccurence = questions_tests.length
      })
  }

  emitDeletedQuestion() {
    const question: Questions = new Questions().fromQuestionsDTO(this.questionComplet.question);
    this.questionDeleted.next(question);
  }

  editQuestion() {
    if (this.questionComplet.professeur.id === this.user.id && this.view !== undefined) {
      this.onViewQuestion(this.questionComplet.question.id);
    }
  }

  emitAddedQuestion() {
    const question: Questions = new Questions().fromQuestionsDTO(this.questionComplet.question);
    if (this.questionsOccurence > 2) {
      if (confirm('This question is has already been used at least 3 times. Are you sure you want to add it ?')) {
        this.questionAdded.next(question);
        this.questionsOccurence++;
      }
    } else {
      this.questionAdded.next(question);
      this.questionsOccurence++;
    }
    console.log(this.questionsOccurence)
  }

  emitRemovedQuestion() {
    const question: Questions = new Questions().fromQuestionsDTO(this.questionComplet.question);
    this.questionRemoved.next(question);
  }
  onViewQuestion(id: number) {
    this.router.navigate(['/questions', 'view', id])
  }


}
