import { Pipe, PipeTransform } from '@angular/core';
import { Cours } from '../Models/Cours';
import { Tests } from '../Models/Tests';
import { TestsDTO } from '../DTO/TestsDTO';

@Pipe({
  name: 'filterProfesseursTests',
})
export class FilterProfesseursTestsPipe implements PipeTransform {

  transform(tests: TestsDTO[], cours: Cours[]): TestsDTO[] {

    let filteredTests: TestsDTO[] = [];
    tests.forEach(test => {
      cours.forEach(c => {
        // console.log(`Cours of test : ${test.cours}`);
        // console.log(`Id of cours : ${c.id}`);
        if (test.cours == c.id) {
          filteredTests.push(test);
          // console.log('true');
        }
        // console.log('---------------');
      });
    })
    console.log(filteredTests)
    return filteredTests;
  }

}
