import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { TestsCompletDTO } from 'src/app/DTO/TestsCompletDTO';
import { ActivatedRoute, Router } from '@angular/router';
import { TestsCompletService } from 'src/app/services/tests-complet.service';
import { TestsComplet } from 'src/app/Models/TestsComplet';

@Component({
  selector: 'app-correctifs',
  templateUrl: './correctifs.component.html',
  styleUrls: ['./correctifs.component.css']
})
export class CorrectifsComponent implements OnInit, OnDestroy {

  testsComplet$: Observable<TestsCompletDTO>;
  private testComplet: TestsComplet;
  private subscriptions: Subscription[] = [];
  private id: number;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private testsCompletService: TestsCompletService) { }

  ngOnInit() {
    this.id = +this.route.snapshot.params['id'];
    this.getTestsCompletObservable(this.id);
    this.getTestsComplet(this.id);
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  getTestsCompletObservable(id: number) {
    this.testsComplet$ = this.testsCompletService.get(id);
  }

  getTestsComplet(id: number) {
    const sub = this.testsCompletService
      .get(id)
      .subscribe(testCompletDTO => {
        this.testComplet = new TestsComplet().fromTestsCompletDTO(testCompletDTO);
        console.log(this.testComplet);
      })
    this.subscriptions.push(sub);
  }

  backToTest() {
    this.router.navigate(['/tests', 'view', this.id]);
  }

}
