import { Component, OnInit, Input, OnDestroy, Output, EventEmitter } from '@angular/core';
import { Solutions } from 'src/app/Models/Solutions';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SolutionsService } from 'src/app/services/solutions.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-single-solution',
  templateUrl: './single-solution.component.html',
  styleUrls: ['./single-solution.component.css']
})
export class SingleSolutionComponent implements OnInit, OnDestroy {

  @Input()
  private solution: Solutions;
  @Input()
  private index: number;
  @Input()
  savedAnswerSelect: number;
  @Output()
  solutionDeleted: EventEmitter<Solutions> = new EventEmitter<Solutions>();
  private previousText: string;
  modifySolutionForm: FormGroup;
  private subscriptions: Subscription[] = [];


  constructor(private formBuilder: FormBuilder,
    private solutionsService: SolutionsService) {
  }

  ngOnInit() {
    this.previousText = this.solution.text;
    console.log(this.solution);
    console.log(this.previousText);
    console.log(this.index)
    this.initForm();
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  emitDeletedSolution() {
    this.solutionDeleted.next(this.solution);
  }

  initForm() {
    this.modifySolutionForm = this.formBuilder.group({
      solution: ['', Validators.required]
    })
  }

  onSubmit() {
    console.log(this.solution);
    const sub = this.solutionsService
      .put(this.solution)
      .subscribe(() => {
        this.launchNotification('Solution has been modified with success ! ')
      });
    this.subscriptions.push(sub);
  }

  launchNotification(notificationMessage: string) {
    const notification = document.querySelector('.notification');
    const message = document.querySelector('.notification > p');
    console.log(message.textContent);
    message.textContent = notificationMessage;
    notification.classList.add('visible');
    const delay = 2000;
    setTimeout(function () {
      notification.classList.remove('visible');
    }, delay);
  }

}
