import { Questions } from './Questions';
import { Professeurs } from './Professeurs';
import { Cours } from './Cours';
import { Solutions } from './Solutions';
import { QuestionsCompletDTO } from '../DTO/QuestionsCompletDTO';

export class QuestionsComplet {
    private _question: Questions;
    private _professeur: Professeurs;
    private _cours: Cours;
    private _solutions: Solutions[];

    constructor(question: Questions = null, professeur: Professeurs = null, cours: Cours = null, solutions: Solutions[] = []) {
        this._question = question;
        this._professeur = professeur;
        this._cours = cours;
        this._solutions = solutions;
    }

    toQuestionsCompletDTO(): QuestionsCompletDTO {
        return {
            question: this._question,
            professeur: this._professeur,
            cours: this._cours,
            solutions: this._solutions
        };
    }

    fromQuestionsCompletDTO(dto: QuestionsCompletDTO): QuestionsComplet {
        Object.assign(this, dto);
        return this;
    }

    get question(): Questions {
        return this._question;
    }

    set question(obj: Questions) {
        this._question = obj;
    }

    get professeur(): Professeurs {
        return this._professeur;
    }

    set professeur(obj: Professeurs) {
        this._professeur = obj;
    }

    get cours(): Cours {
        return this._cours;
    }

    set cours(obj: Cours) {
        this._cours = obj;
    }

    get solutions(): Solutions[] {
        return this._solutions;
    }

    set solutions(obj: Solutions[]) {
        this._solutions = obj;
    }
}