import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SigninFormComponent } from './signin-form/signin-form.component';
import { QuestionListComponent } from './questions/question-list/question-list.component';
import { AuthGuard } from './helpers/auth.guard';
import { SingleQuestionComponent } from './questions/single-question/single-question.component';
import { TestListComponent } from './tests/test-list/test-list.component';
import { SingleTestComponent } from './tests/single-test/single-test.component';
import { CorrectifsComponent } from './correctifs/correctifs/correctifs.component';


const routes: Routes = [
  { path: 'auth/signin', component: SigninFormComponent },
  { path: 'questions', canActivate: [AuthGuard], component: QuestionListComponent },
  { path: 'questions/view/:id', canActivate: [AuthGuard], component: SingleQuestionComponent },
  { path: 'tests', canActivate: [AuthGuard], component: TestListComponent },
  { path: 'tests/view/:id', canActivate: [AuthGuard], component: SingleTestComponent },
  { path: 'correctifs/:id', canActivate: [AuthGuard], component: CorrectifsComponent },
  { path: '', redirectTo: 'questions', pathMatch: 'full' /*prevent bugs on empty paths*/ },
  { path: '**', redirectTo: 'questions' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
