import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SolutionsDTO } from '../DTO/SolutionsDTO';
import { Observable } from 'rxjs';

const URL_API: string = "api/solutions";

@Injectable({
  providedIn: 'root'
})
export class SolutionsService {

  constructor(private http: HttpClient) { }

  query(): Observable<SolutionsDTO[]> {
    return this.http.get<SolutionsDTO[]>(URL_API);
  }

  getByQuestion(idQuestion: number): Observable<SolutionsDTO[]> {
    return this.http.get<SolutionsDTO[]>(`${URL_API}/byquestion/${idQuestion}`);
  }

  get(id: number): Observable<SolutionsDTO> {
    return this.http.get<SolutionsDTO>(`${URL_API}/${id}`);
  }

  post(solutionsDTO: SolutionsDTO): Observable<SolutionsDTO> {
    return this.http.post<SolutionsDTO>(URL_API, solutionsDTO)
  }

  put(solutionsDTO: SolutionsDTO): Observable<any> {
    return this.http.put(URL_API, solutionsDTO);
  }

  delete(id: number): Observable<any> {
    return this.http.delete(`${URL_API}/${id}`);
  }
}
