import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ProfesseursCompletDTO } from 'src/app/DTO/ProfesseursCompletDTO';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TestsService } from 'src/app/services/tests.service';
import { Tests } from 'src/app/Models/Tests';

@Component({
  selector: 'app-add-test-form',
  templateUrl: './add-test-form.component.html',
  styleUrls: ['./add-test-form.component.css']
})
export class AddTestFormComponent implements OnInit {

  @Input()
  professeursComplet: ProfesseursCompletDTO;
  @Output()
  testCreated: EventEmitter<Tests> = new EventEmitter<Tests>();
  addTestForm: FormGroup;

  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.addTestForm = this.formBuilder.group({
      name: ['', Validators.required],
      cours: ['', Validators.required],
    })
  }

  onSubmit() {
    this.emitCreatedTest();
  }

  emitCreatedTest() {
    const test = this.buildTests();
    this.testCreated.next(test);
    this.addTestForm.reset();
  }

  buildTests(): Tests {
    const name = this.addTestForm.get('name').value;
    const cours = +this.addTestForm.get('cours').value;
    const author = this.professeursComplet.professeur.id;
    const test = new Tests();
    test.name = name;
    test.cours = cours;
    test.author = author;
    return test;
  }

}
