import { Pipe, PipeTransform } from '@angular/core';
import { Cours } from '../Models/Cours';
import { QuestionsCompletDTO } from '../DTO/QuestionsCompletDTO';

@Pipe({
  name: 'filterProfesseurs',

})
export class FilterProfesseursPipe implements PipeTransform {

  transform(questionsComplets: QuestionsCompletDTO[], cours: Cours[]): QuestionsCompletDTO[] {

    let qC: QuestionsCompletDTO[] = [];
    questionsComplets.forEach(q => {
      cours.forEach(c => {
        if (q.cours.id == c.id) {
          qC.push(q);
        }
      });
    })
    console.log(qC)
    return qC;
  }

}
