import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { QuestionsCompletDTO } from '../DTO/QuestionsCompletDTO';
import { Observable } from 'rxjs';

const URL_API: string = "api/questionsComplet";

@Injectable({
  providedIn: 'root'
})
export class QuestionsCompletService {

  constructor(private http: HttpClient) { }

  query(): Observable<QuestionsCompletDTO[]> {
    return this.http.get<QuestionsCompletDTO[]>(URL_API);
  }

  get(id: number): Observable<QuestionsCompletDTO> {
    return this.http.get<QuestionsCompletDTO>(`${URL_API}/${id}`);
  }
}
