import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Questions_TestsDTO } from '../DTO/Questions_TestsDTO';
import { HttpClient } from '@angular/common/http';

const URL_API: string = "api/questions_tests";
@Injectable({
  providedIn: 'root'
})
export class QuestionsTestsService {

  constructor(private http: HttpClient) { }

  query(): Observable<Questions_TestsDTO[]> {
    return this.http.get<Questions_TestsDTO[]>(URL_API);
  }

  get(idQ: number, idT: number): Observable<Questions_TestsDTO> {
    return this.http.get<Questions_TestsDTO>(`${URL_API}/${idQ}/${idT}`);
  }

  getByTest(idT: number): Observable<Questions_TestsDTO[]> {
    return this.http.get<Questions_TestsDTO[]>(`${URL_API}/bytest/${idT}`);
  }

  getByQuestion(idQ: number): Observable<Questions_TestsDTO[]> {
    return this.http.get<Questions_TestsDTO[]>(`${URL_API}/byquestion/${idQ}`);
  }

  post(dto: Questions_TestsDTO): Observable<Questions_TestsDTO> {
    return this.http.post<Questions_TestsDTO>(URL_API, dto);
  }

  delete(id_questions: number, id_tests: number): Observable<any> {
    return this.http.delete(`${URL_API}/${id_questions}/${id_tests}`);
  }

  deleteByTest(id_tests: number): Observable<any> {
    return this.http.delete(`${URL_API}/bytest/${id_tests}`);
  }

  // delete(id: number): Observable<any> {
  //   return this.http.delete(`${URL_API}/${id}`);
  // }
}
