import { Questions_TestsDTO } from '../DTO/Questions_TestsDTO';

export class Questions_Tests {

    private _id_questions: number;
    private _id_tests: number;

    constructor(id_questions: number = -1, id_tests: number = -1) {
        this._id_questions = id_questions;
        this._id_tests = id_tests;
    }

    toQuestions_TestsDTO(): Questions_TestsDTO {
        return {
            id_questions: this._id_questions,
            id_tests: this._id_tests
        };
    }


    fromQuestion_TestsDTO(dto: Questions_TestsDTO): Questions_Tests {
        Object.assign(this, dto);
        return this;
    }

    public get id_questions(): number {
        return this._id_questions;
    }

    public set id_questions(value: number) {
        this._id_questions = value;
    }

    public get id_tests(): number {
        return this._id_tests;
    }

    public set id_tests(value: number) {
        this._id_tests = value;
    }
}