import { Pipe, PipeTransform } from '@angular/core';
import { TestsCompletDTO } from '../DTO/TestsCompletDTO';
import { TestsDTO } from '../DTO/TestsDTO';

@Pipe({
  name: 'filterSearchTest'
})
export class FilterSearchTestPipe implements PipeTransform {

  transform(allTests: TestsDTO[], searchInput: string): TestsDTO[] {
    let tests: TestsDTO[] = [];
    if (searchInput == '') {
      return allTests;
    }
    allTests.forEach(t => {
      console.log(t.name.toLowerCase());
      if (t.name.toLowerCase().includes(searchInput.toLowerCase())) {
        tests.push(t);
      }
    })
    return tests;
  }
}
