import { Pipe, PipeTransform } from '@angular/core';
import { QuestionsComplet } from '../Models/QuestionsComplet';

@Pipe({
  name: 'filterQuestionByCours'
})
export class FilterQuestionByCoursPipe implements PipeTransform {

  transform(questionsComplets: QuestionsComplet[], selectedOption: number): QuestionsComplet[] {
    console.log(questionsComplets)
    let qC: QuestionsComplet[] = [];
    questionsComplets.forEach(q => {
      if (q.cours.id == selectedOption) {
        qC.push(q);
      }
    })
    console.log(qC)
    return qC;
  }

}
