import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { Professeurs } from '../Models/Professeurs';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {
  private currentUserSubject: BehaviorSubject<Professeurs>;
  private currentUser: Observable<Professeurs>;

  constructor(private http: HttpClient) {
    this.currentUserSubject = new BehaviorSubject<Professeurs>(JSON.parse(localStorage.getItem("currentUser")));
    this.currentUser = this.currentUserSubject.asObservable();
  }

  public get currentUserValue(): Professeurs {
    return this.currentUserSubject.value;
  }

  public get currentUserSub(): BehaviorSubject<Professeurs> {
    return this.currentUserSubject;
  }

  login(mail: string, password: string) {
    return this.http.post<any>("api/professeurs/authenticate", { mail, password })
      .pipe(map(prof => {
        localStorage.setItem('currentUser', JSON.stringify(prof));
        this.currentUserSubject.next(prof);
        return prof;
      }));
  }

  logout() {
    localStorage.removeItem("currentUser");
    this.currentUserSubject.next(null);
  }
}
